body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body html {
  color: #fff;
}

.App {
  text-align: center;
}

p {
  font-size: 1.5rem;
}

h3 {
  font-size: 2rem;
}


.navbar {
  margin-bottom: 0px!important;
}

.item img{
  -webkit-filter: brightness(.65);
          filter: brightness(.65);
}

#header {
  width: 100vw;
}



.carousel-inner>.item>a>img {
  width: 100%!important;
  height: 100vh;
}

#header .carousel-indicators {
  visibility: hidden;
}

#header .carousel-control {
  visibility: hidden;
}

#header .carousel-caption {
  text-align: left;
}





h2 {
  color: #fff!important;
  text-align: left;
  letter-spacing: .4em;
  margin-top: 20px;
}


#card {
  margin: 50px auto 10px;
  flex: 1 1;
  width: 10rem;
  border: none;
  border-radius: 10px;
  background: transparent;
}


.card-title {
  color: #fff;
  text-align: center;
  font-size: 1.25rem;
}

.card-body {
  color: rgba(255,255,255, .7);
  text-align: left;
}

.card img {
  width: auto;
  height: 180px;
  border-radius: 10px;
  margin-right: 15px;
  transition: 2s;
}

.card-img:hover {
  height: 200px;
}

.main-footer {
  background-color: #131517;
  color: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 4rem;
  padding: 7rem;
}

.main-footer-top-header {
  text-align: left;
  margin-bottom: 4rem;
}

.main-footer-top-item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  text-align: left;
  padding-bottom: 40px;
}

.main-footer-top-rights {
  text-align: left;
}

.main-footer-bottom {
  text-align: right;
}

.main-footer-bottom-img {
  width: 250px;
}

@media (min-width: 1200px) {

}

body {
    color: #fff!important;
}

.item-details-header {
    height: 100vh;
    width: 100vw;
    background-size: cover!important;
    padding: 3% 10%;
}


.item-details-info-container {
    padding-top: 38%;
}

.items-details-info-container-content-title {
    color: #fff;
}

.items-details-info-container-content-rating {
    font-size: 1.7rem;
    color: rgba(255, 255, 255, .8);
}

.items-details-info-container-content-genre {
    color: rgba(255, 255, 255, .5);
    font-size: 1.2em;
}

.item-details-main {
    margin: 0 auto;
    max-width: 70%;
    padding: 4rem;
}

.item-details-main-title {
    letter-spacing: .3em;
    -webkit-margin-before: .83em;
            margin-block-start: .83em; 
    -webkit-margin-after: .83em; 
            margin-block-end: .83em; 
    text-transform: uppercase;
}

.item-details-main-overview {
    font-size: 1.7em;
    color: rgba(255, 255, 255, .5);
}


@media only screen and (max-width: 1200px) {
    .item-details-info-container {
        padding-top: 45%;
    }
}

@media only screen and  (max-width: 900px) {
    .item-details-info-container {
        padding-top: 53%;
    }
}
